@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  overflow-x: hidden !important;
}
body {
  font-family: 'Roboto', Arial, sans-serif, Helvetica;
  line-height: 1.4;
  color: #333;
  background-color: rgb(247, 247, 247);
  overflow-x: hidden;
  font-family: 'Roboto';
}

/* utility classes */
.container {
  width: 90%;
  height: 100%;
  margin: auto;
}

.text-primary {
  color: #3693ff;
}
.big-light {
  background: #f4f4f4f4;
  color: #fff;
}
.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #444;
  border-radius: 4px;
  background: green;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #444;
  border-color: #fcc102;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}

.modal {
  position: fixed;
  top: 1%;
  left: 30%;
  transform: translate(30% - 50%);
  background-color: #fff;
  width: 600px;
  height: 500px;
  z-index: 2000;
}

.user-profile-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}
.modal__footer {
  background-color: #444;
  padding: 10px;
  display: flex;
  justify-content: center;
  /* align-items: center; */
}
.modal__header {
  background-color: rgba(12, 158, 44, 0.959);
  color: #fff;
}
.modal__header h1 {
  font-size: 20px;
  margin-top: 5px;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.form-control {
  margin: 1rem 0;
  width: 60%;
  margin: 10px 10px;
}

.form-control label,
.form-control input,
.form-control textarea {
  display: block;
}

.form-control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.form-control input,
.form-control textarea {
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  background: #f8f8f8;
  padding: 0.15rem 0.25rem;
}

.form-control input:focus,
.form-control textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input,
.form-control--invalid textarea {
  border-color: red;
  background: #ffd1d1;
}

.form-newPost {
  background-color: rgb(79, 95, 95);
  color: #fff;
  padding: 10px;
}
.new-post > form {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.new-post input,
.new-post label,
.new-post textarea {
  margin-left: 40%;
}

.admin-dashbord {
  background-color: #000;
  width: 300px;
  height: auto;
  margin: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
  color: #fff;
  /* margin-bottom: 20px; */
}

.admin-dashbord img {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  margin-left: 35px;
  margin-top: 10px;
}
.admin-dashbord img:hover {
  cursor: pointer;
}

.admin-dashbord h4 {
  position: absolute;
  margin: 20px;
  margin-left: 100px;
}
.admin-dashbord h4 strong {
  font-size: 10px;
  color: #fcc102;
  padding: 3px;
}
.admin-dashbord h4 span {
  font-size: 15px;
  font-weight: 900;
  color: #053e74;
}
.active-link {
  color: #fff;
  background-color: #053e74;
}
.admin-dashbord button {
  display: block;
  margin: 15px;
  padding: 10px;
  border: none;
  outline: none;
  box-shadow: 0 2px 4px rgba(0, 0, 0.7);
  background-color: darkgreen;
  width: 60%;
  margin-left: 40px;
  color: #fff;
  font-weight: 900;
}
.admin-dashbord button:hover {
  background-color: #625b9c;
  transition: 1s;
  color: rgb(19, 226, 140);
  cursor: pointer;
}
.admin-dashbord ul li a {
  font-size: 10px;
  color: rgb(19, 226, 140);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: 900;
}
.admin-dashbord ul li a:hover {
  color: rgb(19, 226, 140);
  cursor: pointer;
}
.admin-dashbord h4 {
  font-size: 16px;
}
.menu-icon {
  display: none !important;
}

.admin-dashbord ul li a:active {
  background-color: #444;
  color: rgb(19, 226, 140);
}
.new-post {
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  /* height: 1000px; */
}
.new-post-modal {
  background-color: #444 !important;
  width: 60% !important;
  margin-top: -3% !important;
  margin-left: -6% !important;
  height: 75vh;
}
.new-modal-botton {
  background-color: red !important;
}
@media (max-width: 767px) {
  .admin-dashbord {
    display: none;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .admin-dashbord {
    display: visible;
    margin-top: 20%;
  }
}

/* active POST */
.ditails {
  background-color: #fff;
  position: absolute;
  top: 0;
  right: 20px;

  width: 70%;
  margin: 20px 10px 10px;
  box-shadow: 0 0 3px rgba(9, 49, 29, 0.3);
}

.menu-icon {
  display: hidden !important;
}
.active {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-shadow: 0 0 3px rgba(9, 49, 29, 0.3);
  padding: 10px;
  padding: 10px;
}
.active select {
  background-color: #fff;
  border: 1px solid darkgreen;
  outline: none;
  border-radius: 5px;
  font-size: 20px;
  margin-left: 10px;
  padding: 5px 15px 5px 15px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}
.active select option {
  font-size: 20px;
}
/* .active h3 {
  position: absolute;
  margin: 10px;
  color: darkgreen;
  font-weight: 900;
} */
.active h3 span {
  font-size: 15px;
  color: #444;
}

.active select:hover {
  cursor: pointer;
}
.post-active option {
  font-size: 10px;
}

@media (max-width: 768px) {
  .ditails {
    width: 90%;
    font-size: 13px;
    display: flex;
    justify-content: center;
  }
  .menu-icon {
    display: visible !important;
  }
}

.chart {
  width: 350px;
  /* height: 300px; */
  display: flex;
  background-color: #fff;
  position: absolute;
  /* padding: 30px; */
  top: 90px;
  left: 28%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-shadow: 0 1px 4px black;
  border-radius: 10px;
}
.chart:hover {
  padding: 2px;
  cursor: move;
}
.chart button {
  padding: 10px;
  width: 50%;
  margin: 0 auto 10px;
  background-color: darkgreen;
  border: none;
  outline: none;
  color: #fff;
  border-radius: 5px;
}
.chart button h6 {
  font-size: 18px;
  font-style: normal;
  font-style: initial;
  font-weight: 900;
}
.chart button:hover {
  background-color: #444;
  color: #fff;
  cursor: pointer;
}

@media (max-width: 768px) {
  .chart {
    left: calc(100%-60%);
    width: 350px;
    margin-top: -10px;
    margin-left: -110px;
    display: flex;
    left: 35%;
    /* left: 27%; */
    width: 90%;
    margin-bottom: 40%;
    justify-content: center;
    justify-content: space-around;
  }
  .chart button h6 {
    font-size: 10px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .chart {
    width: 350px;
    margin-top: 20px;
    margin-left: 60px;
    display: flex;
    width: 50%;
    margin-bottom: 40%;
    justify-content: flex-start;
    justify-content: space-around;
  }
}

