table {
  background-color: rgb(107, 103, 103);
  margin-top: 30px;
  font-size: 15px;
  width: 70%;

  border-collapse: collapse;
  color: #fff;
  box-shadow: 0 0 3px rgba(9, 49, 29, 0.3);
  margin: 20px;
  margin-left: 28%;
  /* margin-top: -10%; */
}
thead,
th,
tbody,
td {
  text-align: center;
  border: 3px solid rgba(19, 226, 140, 0.5);
  padding: 5px;
}

thead {
  color: #fcc206;
  font-size: 20px;
  font-weight: 900;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
td:hover {
  background-color: darkgreen;
  cursor: pointer;
  padding: 1px;
  border: 1px solid white;
}

.number {
  color: rgb(19, 226, 140);
  background-color: #000;
  font-weight: 900;
  border-bottom: 7px solid wheat;
  /* border-top: 4px solid #000; */
}
thead {
  background-color: #000;
}

@media (max-width: 768px) {
  table {
    position: absolute;
    bottom: 10px;
    width: 100%;
    margin: 90px -0.5% 0;
  }
}

.admin {
  background-color: rgb(247, 247, 247);
