  .admin {
    min-height: 1000px;
  }
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3.5rem;
  height: 1px;
  background: darkgreen;
}

.main-navigation__title {
  color: white;
  margin: 1rem;
}
.main-navigation__title img {
  width: 50px;
  height: 50px;
  position: absolute;
  right: 10px;
  top: 10px;
}

.main-navigation__title a {
  text-decoration: none;
  color: #444;
  visibility: visible;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
  .main-navigation__title a {
    text-decoration: none;
    color: #444;
    visibility: hidden;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .nav-links a {
    font-size: 15px;
    /* margin-left: -10px; */
  }
}

.main-header {
  width: 100%;
  height: 6rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  padding: 0 1rem;
  z-index: 10;
}

main {
  margin-top: 6rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 85px;
  height: 85px;
  position: absolute;
  top: 10px;
  left: 20px;

  background-color: none;
}
.image-profile {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}
.nav-links li {
  margin: 1rem;
}

.color-active {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0 1rem;
  margin: 3px;
}

.color-active:hover,
.color-active:active,
.color-active.active {
  background-color: #444;
  color: #fff;
  box-shadow: 0 1px 3px rgba(0, 0, 00.6);
  border-radius: 4px;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: darkgreen;
  color: #fff;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: darkgreen;
    text-decoration: none;
    font-size: 20px;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: darkgreen;
    color: #fff;
  }
  .nav-links img {
    visibility: visible;
    border-radius: 100%;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .nav-links img {
    visibility: hidden;
  }

  .nav-links a {
    color: darkgreen;
    text-decoration: none;
    font-size: 10px;
  }
}

footer {
  text-align: center;
  height: auto;
  border-radius: 10px;
  overflow: hidden;
  margin: 0;
  background-color: rgb(4, 12, 4);
}

footer > div {
  width: 100px;
  min-height: auto;
  display: inline-block;
  margin: 10px 10px;
}
.about {
  height: auto;
  padding: 6px;
  width: 50%;
  text-align: center;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5);
  border: 1px solid #fff;
  border-radius: 50px;
  font-family: 'Roboto';
  font-size: 15px;
  color: #fff;
  margin: 10px;
}

.about p {
  font-size: 14px;
}

footer > div.about h3 {
  margin: 15px 0;
}
.footer-image {
  width: 100px;
  height: 100px;
}
.about h3::after {
  content: '';
  display: block;
  margin: 0 auto;
  width: 50%;
  border-bottom: 4px solid green;
  margin-top: 4px;
}

.about button {
  font-size: 15px;
  margin: 10px 0;
  padding: 10px;
  background-color: darkgreen;
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 20px;
  margin: 10px 0 0;
  outline: none;
  cursor: pointer;
}

.about button:hover {
  background-color: #fff;
  color: darkgreen;
  transition: 0.5s;
  font-size: 15px;
  border: 2px solid green;
  border-radius: 10px;
}

.link {
  width: 350px;
  height: 200px;
  border-radius: 5px;
  text-align: left;
  border: none;
  background: rgba(49, 158, 113, 0.6);
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5);
  padding: 10px;
}
.link h3 {
  font-size: 15px;
  margin: 10px;
  font-weight: bold;
  color: #fff;
}
.link h3::after {
  content: '';
  display: block;
  margin: 3px 4px 3px;
  width: 20%;
  border-bottom: 2px solid #fff;
}

.link h5 a {
  color: #fff;
  display: inline-block;
  font-size: 15px;
  padding: 5px 10px;
  margin: 0 10px;
  text-decoration: none;
  list-style: none;
  margin-left: 35px;
}
.link h4,
h3,
h5:hover {
  cursor: pointer;
}
.link h5 a:hover {
  background-color: #fff;
  color: darkgreen;
}

.social {
  position: absolute;
  right: 0;
  /* width: 400px; */
  height: 200px;
  background-color: rgb(158, 155, 155, 0.5);
  background: rgba(49, 158, 113, 0.6);
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5);
  border: 1px solid #fff;
  border-radius: 50px;
}
.social h3 {
  text-align: center;
  margin: 10px 0;
  color: #fff;
}
.social h3::after {
  content: '';
  display: block;
  margin: 0 auto;
  width: 30%;
  border-bottom: 1px solid #fff;
  margin-top: 4px;
}
.follow {
  text-align: center;
}
footer i {
  font-size: 40px;
  margin: 0 10px;
  color: #fff;
}
.social h4 {
  text-align: center;
  font-size: 20px;
  font-weight: 200;
  margin-top: 20px;
  color: #fff;
}

.app-download {
  text-align: center;
  font-size: 30px;
}
.app-download i {
  color: #fff;
  margin: 10px 5px 0;
}
i:hover {
  cursor: pointer;
  color: #fff;
}
.fo {
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  color: #fff;
}
@media (max-width: 768px) {
